.price {
	text-align: center;
	&__title {
		margin: auto;
		margin-bottom: 60px; }
	&__subtitle {
		font-size: 20px;
		margin-bottom: 60px; }
	&__image {
		display: block; } }
.card {
	margin-bottom: 30px;
	border: 1px solid #e8e8e8;
	transition: 0.2s;
	&:hover {
		box-shadow: 0 20px 25px rgba(0, 0, 0, 0.1); }
	&s {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap; }
	&__text {
		font-size: 18px;
		padding: 40px; }
	&__title {
		color: #000; }
	&__image {
		position: relative;
		display: block;
		&::before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			background-image: linear-gradient(to top, #ffd200 0%, rgba(255, 255, 255, 0) 100%);
			opacity: 0;
			transition: 0.2s; }
		&:hover {
			&::before {
				opacity: 1; } } }
	&__link {
		display: inline-block;
		text-decoration: none;
		font-family: 'Museo Sans Bold';
		color: #333;
		position: relative;
		&:after {
			content: '';
			display: block;
			width: 100%;
			height: 3px;
			background-color: #ffd200;
			position: absolute;
			bottom: -4px;
			left: 0;
			z-index: -1;
			transition: 0.2s; }
		&:hover {
			&:after {
				height: 12px; } } } }
@media (max-width: 768px) {
	.cards {
		justify-content: center; } }
