.offer {
	background: url('../img/offer.jpg') no-repeat;
	background-position: center right;
	background-size: contain;
	position: relative;
	&:before {
		content: '';
		width: 50%;
		height: 100%;
		position: absolute;
		background-color: #fff;
		top: 0;
		left: 0; }
	&-block {
		position: relative;
		width: 50%;
		padding-right: 90px; }
	&__title {
		text-align: left;
		max-width: 360px;
		font-size: 24px;
		line-height: 35px;
		margin-bottom: 24px; }
	&__subtitle {
		max-width: 240px;
		margin-bottom: 30px; }
	&__input {
		margin-bottom: 30px;
		width: 240px;
		&:first-child {
			margin-right: 10px; } }
	&__button {
		width: 100%;
		min-height: 70px;
		padding: 0px 20px;
		font-size: 16px; } }
@media (max-width: 1200px) {
	.offer {
		background-size: cover;
		&__input {
			width: 100%;
			margin-bottom: 10px; }
		&__button {
			font-size: 14px; } } }
@media (max-width: 768px) {
	.offer {
		background: #fff;
		&-block {
			width: 100%;
			padding-right: 0px;
			padding: 0px 10px; }
		&__title {
			text-align: center;
			margin: auto;
			margin-bottom: 20px;
			max-width: 461px;
			font-size: 18px; }
		&__subtitle {
			text-align: center;
			margin: auto;
			margin-bottom: 45px;
			max-width: 465px;
			font-size: 14px; }
		&__input {
			font-size: 14px; } } }
