.hero {
	background-color: #f5f5f5;
	padding: 50px 0px;
	&-block {
		display: flex; }
	&-text {
		flex-basis: 50%;
		padding-right: 100px;
		&__title {
			font-size: 41px;
			line-height: 55px;
			margin-bottom: 40px;
			font-weight: 700;
			font-family: 'Museo Sans Bold', sans-serif; }
		&__subtitle {
			font-size: 20px;
			display: inline-block;
			margin-bottom: 50px; } }
	&-list {
		margin: 0;
		padding: 0;
		&__item {
			display: flex;
			align-items: center;
			margin-bottom: 25px;
			&:last-child {
				margin-bottom: 0; } }
		&__image {
			width: 40px;
			height: 40px;
			border: 2px solid #ffd200;
			display: flex;
			align-items: center;
			justify-content: center;
			border-radius: 50%;
			margin-right: 30px; }
		&__text {
			max-width: 300px; } } }
@media (max-width: 1200px) {
	.hero {
		&-block {
			flex-direction: column; }
		&-list {
			margin-bottom: 44px; }
		&-text {
			padding-right: 10px; } } }
@media (max-width: 768px) {
	.hero {
		&-text {
			&__title {
				font-size: 24px;
				line-height: 1.4;
				font-weight: 800;
				margin-bottom: 15px; }
			&__subtitle {
				font-size: 16px;
				margin-bottom: 28px; } }
		&-list {
			&__item {
				margin-bottom: 20px; }
			&__text {
				max-width: 200px;
				font-size: 12px; }
			&__image {
				margin-right: 15px; } } } }
