* {
	box-sizing: border-box; }
body {
	font-family: 'Museo Sans Light'; }

.wrapper {
	overflow: hidden;
	width: 100%;
	min-height: 100%;
	display: flex;
	flex-direction: column; }

img {
	max-width: 100%; }
.container {
	width: 1170px;
	margin: auto; }
.button {
	border-radius: 10px;
	border: none;
	background-color: #ffd200;
	font-family: 'Museo Sans Bold';
	&:hover {
		cursor: pointer;
		background-color: #ebb100;
		color: #fff; } }
.section {
	padding: 90px 0px;
	&-title {
		text-align: center;
		font-size: 30px;
		font-family: 'Museo Sans Bold';
		font-weight: 700;
		max-width: 500px;
		line-height: 45px; }
	&-subtitle {
		display: block;
		font-size: 16px;
		font-weight: 400; } }
.input {
	border: 1px solid #dddddd;
	padding: 20px 26px;
	font-size: 16px;
	border-radius: 10px;
	font-family: 'Museo Sans Light'; }
.arrows {
	&__left, &__right {
		&:hover {
			cursor: pointer; } } }
.pluse {
	background: #ffc000;
	font-size: 32px;
	color: #000;
	line-height: 50px;
	text-align: center; }

@media (max-width: 1200px) {
	.container {
		width: 760px; }
	.pluse {
		padding: 50px 30px;
		font-size: 25px; }
	.pluse-text {} }
@media (max-width: 768px) {
	.container {
		max-width: 580px;
		width: 95%; }
	.section {
		&-title {
			font-size: 19px;
			padding: 0px 10px; }
		&-subtitle {} }
	.pluse {
		padding: 50px 30px;
		font-size: 20px;
		line-height: 38px; } }
