.brif {
	background-image: url('../img/brif/brif-color-bg.jpg'), url('../img/brif/brif-bg.jpg');
	background-position: left center, center right;
	background-size: 50%, contain;
	background-repeat: repeat-y , no-repeat;
	&-block {
		display: flex; }
	&__interview {
		flex-basis: 50%; }
	&__form {
		flex-basis: 50%;
		padding-left: 200px;
		text-align: center; }
	&__input {
		width: 100%;
		max-width: 370px;
		margin-bottom: 15px; }
	&__button {
		font-size: 16px;
		font-family: 'Museo Sans Bold';
		line-height: 18px;
		height: 70px;
		width: 100%;
		max-width: 370px;
		margin-bottom: 30px; } }
.interview {
	padding-right: 70px;
	&__title {
		font-size: 22px;
		line-height: 33px;
		font-weight: 700;
		font-family: 'Museo Sans Bold';
		margin-bottom: 50px; }
	&__subtitle {
		font-size: 16px;
		line-height: 45px;
		display: block;
		margin-bottom: 80px; }
	&-list {
		list-style: none;
		padding: 0;
		&__item {
			display: flex;
			align-items: center;
			font-family: 'Museo Sans Bold';
			margin-bottom: 25px; }
		&__counter {
			font-size: 14px;
			display: inline-block;
			border-radius: 50%;
			margin-right: 30px;
			text-align: center;
			line-height: 35px;
			width: 35px;
			height: 35px;
			border: solid 2px #000000; }
		&__text {
			max-width: 400px;
			font-size: 18px; } } }
.form {
	&__title {
		font-size: 22px;
		line-height: 33px;
		font-weight: 700;
		font-family: 'Museo Sans Bold';
		margin-bottom: 50px; }
	&__subtitle {
		font-size: 16px;
		line-height: 45px;
		display: block;
		margin-bottom: 80px; } }
@media (max-width: 1200px) {
	.brif {
		background-size: 50%, cover;
		&__interwiev {}
		&__form {
			padding-right: 20px;
			padding-left: 50px; } }
	.interview {
		padding-right: 30px;
		&-list {
			&__counter {
				min-width: 35px;
				min-height: 35px; }
			&__text {
				font-size: 14px;
				max-width: 270px; } }
		&__title {
			font-size: 20px;
			margin-bottom: 0; }
		&__subtitle {
			margin-bottom: 50px; } }
	.form {
		&__title {
			margin-bottom: 30px;
			font-size: 20px; }
		&__subtitle {
			margin-bottom: 50px; } } }
@media (max-width: 768px) {
	.brif {
		background-size: 100% 51%, cover;
		background-position: top center, center bottom;
		background-repeat: repeat-x, no-repeat;
		&-block {
			flex-direction: column; }
		&__interview {
			flex-basis: 100%;
			margin-bottom: 40px; }
		&__form {
			margin-top: 60px;
			flex-basis: 100%;
			padding-left: 10px;
			padding-right: 10px; }
		&__button {
			display: block;
			margin: auto;
			margin-bottom: 30px; } }
	.interview {
		padding-left: 30px;
		&__title {
			padding-right: 7px; }
		&-list {
			&__counter {
				margin-right: 10px; } } } }




