.footer {
	position: relative; }

.map {
	left: 0;
	top: 0;
	width: 50%;
	position: absolute; }


.contacts {
	flex-basis: 50%;
	padding-top: 100px;
	padding-left: 200px;
	&-block {
		display: flex;
		justify-content: flex-end; }
	&__title {
		font-family: 'Museo Sans Bold';
		font-size: 22px;
		line-height: 1.4;
		margin-bottom: 100px; }
	&-list {
		padding: 0;
		list-style: none;
		&__item {
			display: flex;
			align-items: flex-start;
			margin-bottom: 39px; }
		&__icon {
			margin-right: 23px; }
		&__image {
			width: 18px;
			height: 20px;
			text-align: center;
			line-height: 20px; }
		&__text {} } }

@media (max-width: 1200px) {
	.contacts {
		padding-left: 50px; } }

@media (max-width: 768px) {
	.footer {}
	.map {
		width: 100%;
		top: 590px; }
	.contacts {
		padding-right: 10px;
		padding-top: 50px;
		flex-basis: 100%;
		padding-left: 10px;
		&__title {
			font-size: 20px; } } }




