.section-maps {
	display: flex; }
.map {
	&-block {
		flex-basis: 50%;
		width: 50%; }
	&-info {
		flex-basis: 50%;
		padding-left: 215px;
		&__img {
			height: 20px;
			width: 20px; }
		&__title {
			margin-bottom: 90px;
			font-size: 22px;
			font-weight: 700;
			margin-top: 60px;
			font-family: 'Museo Sans Bold'; }
		&__list {
			font-size: 16px;
			font-family: 'Museo Sans Bold';
			max-width: 450px;
			text-decoration: none;
			margin: 0;
			padding: 0;
			display: flex;
			justify-content: space-between;
			flex-flow: column;
			text-align: left; }
		&__item {
			list-style-type: none;
			position: relative;
			margin-bottom: 30px;
			&::before {
				content: '';
				background: url('../img/maps/1.png') center no-repeat; } }
		&__text {
			max-width: 250px; }
		&__image {
			display: inline-block;
			margin-right: 15px; } } }
