.steps {
	&__title {
		max-width: 660px;
		margin: auto;
		margin-bottom: 59px; }
	&__subtitle {
		font-size: 20px;
		text-align: center;
		margin-bottom: 69px; }
	&__text {
		font-size: 16px;
		color: #000;
		line-height: 24px; }
	&__images {
		margin: auto;
		margin-bottom: 30px;
		max-width: 100px;
		max-height: 100px;
		padding: 30px;
		border: 3px solid #ffd200;
		border-radius: 50%; }
	&__image {
		width: 35px;
		height: 35px; }
	&-block {
		flex-basis: 20%;
		text-align: center;
		padding: 0px 5px;
		&_l {
			flex-basis: 20%;
			text-align: center;
			padding: 0px 5px; } }
	&-blocks {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap; } }

@media (max-width: 1200px) {
	.steps {
		&-block {
			flex-basis: 50%;
			margin-bottom: 30px;
			&_l {
				flex-basis: 100%; } }
		&-blocks {
			display: flex; } } }

@media (max-width: 768px) {
	.steps {
		&-block {
			padding: 0px 30px;
			flex-basis: 100%;
			margin-bottom: 30px;
			&_l {
				flex-basis: 100%; } } } }
