.contracts {
	text-align: center;
	color: #000;
	&__title {
		margin: auto;
		margin-bottom: 90px;
		max-width: 650px; }
	&-block {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap; }
	&-item {
		flex-basis: 33%; }
	&__image {
		margin-bottom: 40px; }
	&__subtitle {
		max-width: 287px;
		margin: auto; }
	&__paragraf {
		font-size: 18px;
		line-height: 24px; } }

@media (max-width: 1200px) {
	.contracts {
		&-block {
			display: flex; }
		&-item {
			flex-basis: 100%; }
		&__paragraf {
			margin-bottom: 40px; } } }

@media (max-width: 768px) {
	.contracts {
		&-block {
			display: flex; }
		&-item {
			flex-basis: 100%; } } }
