.navbar {
	padding: 8px 0px;
	&-block {
		display: flex;
		justify-content: space-between;
		align-items: center; }
	&__info {
		display: flex;
		align-items: center;
		text-align: right; }
	&__address {
		display: flex;
		flex-direction: column;
		margin-bottom: 9px; }
	&__button {
		margin-left: 30px;
		padding: 15px 30px; }
	&__phone {
		font-family: 'Museo Sans Bold', sans-serif;
		color: #333;
		font-size: 20px;
		font-weight: 400;
		text-decoration: none; } }
@media (max-width: 768px) {
	.navbar {
		&__contacts {
			display: none; }
		&__button {
			font-size: 12px;
			padding: 15px 26px; }
		&__logo {
			width: 90px; } } }
